<template>
  <div class="uk-card">
    <div class="uk-card-header">
      <img :src="img_source">
    </div>
    <div class="uk-card-body">
      <div>
         : {{ apartment_type }}
      </div>
      <div>
         : {{ area }}  <sup>2</sup>
      </div>
      <div>
        : {{ balcony }}
      </div>
      <div>
        : {{ loggia }}
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'BuildingsLayout',
  props: {
    img_source: {
      type: String,
      required: true
    },
    apartment_type: String,
    area: {
      type: Number,
      required: true
    },
    balcony: Number,
    loggia: Number
  }
}
</script>

<style scoped>

</style>



